<template>
  <div class="powerSystem">
    <v-container>
      <v-row justify-center dense>
        <v-col cols="12" sm="6" md="6" lg="3"><PowerCard :cursor="'pointer'" :service="'tech'"/></v-col>
        <v-col cols="12" sm="6" md="6" lg="3"><PowerCard :cursor="'pointer'" :service="'hvac'"/></v-col>
        
      </v-row>
      <v-row>
         <v-col  cols="12" sm="12" md="12"><PowerLogs /></v-col>
      </v-row>
    </v-container>

    <ReadingsDetails v-if="dialog" v-model="dialog" :id="activeID"/>
  </div>
</template>

<script>

import PowerCard from '@/components/PowerCard.vue'
import PowerLogs from '@/components/PowerLogs.vue'
import eventBus from "../eventBus"

import ReadingsDetails from '@/components/ReadingsDetailDialog.vue'

export default {
  name: 'powerSystem',
  components: {
    PowerCard,
    PowerLogs,
    ReadingsDetails
  },
  data: () => ({
    view:{},
    activeID:'',
    readingsObj:{},
    dialog:false,
    cursor:'pointer',
    logs:{},
    log2:[],
    counter:0
  }),
  created(){
    eventBus.$on('details',this.openDetailsDialog)
  },
  mounted (){
    window.scrollTo(0, 0)
  },
  methods:{
    power(msg){
      this.logs = msg
      let array = []
      let array2 = []
      msg.forEach(element => {
        //console.log(element.Time)
        array.push(element.Time)
      });
      this.counter ++
      this.log2 = array
      if(array === array2){
        alert('duplicate log')
      } else {
        array2 = array
      }
      
    },
    openDetailsDialog(id){
      this.activeID = id
      this.dialog = !this.dialog
    },
  },
}
</script>
<style scoped>
 .container {
    width: 100%;
    padding: 5px;
   /*  margin-right: 0px; 
    margin-left: 0px; */ 
} 
.fs-13{
  font-size: 12px;
  
}
.v-subheader{
  padding: 0px;
}
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}
pre {
  background-color: black;
}
.powerSystem {
    overflow: hidden;
  }



</style>