<template>
    <v-card min-height="462px">
        <v-card-title class="text-center pt-1">Power System Logs </v-card-title>
        <v-card-text v-if="showFTP">
        <!-- <v-select :items="monthSelect" label="Month" outlined v-model="currentMonth" class="px-16"></v-select>
        <v-select :items="daySelect" label="Day" outlined v-model="currentMonth"></v-select> -->
        <v-container class="pt-0">
            <v-row dense >
                <v-col cols="12" sm="12" md="4" lg="2">
                    <v-select   :items="month" label="Month" outlined v-model="monthSelect" class="" @click="hide('month')" @input="dropDown('day')"></v-select>
                </v-col>
                <v-col cols="12" sm="12" md="4" lg="2">
                    <v-select v-if="monthSelect" :items="day" label="Day" outlined v-model="daySelect" class="fs-14" @click="hide('day')" @input="dropDown('hour')" ></v-select> 
                </v-col>
                <v-col cols="12" sm="12" md="4" lg="2">
                    <v-select v-if="daySelect" :items="hour" label="Hour" outlined class="fs" v-model="hourSelect" @click="hide('hour')" @input="dropDown('logFile')"></v-select>
                </v-col>
                <v-col cols="12" sm="12" md="4" lg="3">
                    <v-select v-if="hourSelect" :items="logFile" label="10 Minute Log File" outlined class="fs" v-model="logFileSelect" ></v-select>
                </v-col>
                <v-col cols="12" sm="12" md="4" lg="3">
                    <!-- <v-btn  small class="" @click="getLogs" :loading="load">load</v-btn><v-btn  small>download</v-btn> --><v-btn v-if="logFileSelect" @click="quickLogs" >quick view</v-btn>
                </v-col>
            </v-row>
            <v-row>
                <v-col>
                    <v-card outlined >
                        <v-card-subtitle>Coming Soon....</v-card-subtitle>
                        <v-card-subtitle>Use the above drop downs with the Quick View button for now.</v-card-subtitle>
                    <v-data-table 
                       :headers="headers"
                       multi-sort
                        :items="test"
                        class="elevation-16 py-8"
                        dense
                        :loading="load"
                        loading-text="Loading... Please wait"
                        :items-per-page="15"
                    >
                    <!-- <template  >
                         
                        <thead>
                        <tr>
                            <th colspan="2">Category 1</th>
                            <th colspan="3">Category 2</th>
                        </tr>
                        </thead>
                    </template> -->
                        <template v-slot:header="{ props: {  } }" fixed-header >
                            <thead>
                            <tr>
                                <th :colspan="3" class="text-center">
                                Tech Camlocks Volts
                                </th>
                                <th :colspan="3" class="text-center">
                                Tech Camlocks Amps
                                </th>
                                <th :colspan="3" class="text-center">
                                HVAC Camlocks Volts
                                </th>
                                <th :colspan="3" class="text-center">
                                HVAC Camlocks Amps
                                </th>
                                <th :colspan="2" class="text-center">
                                Time
                                </th>
                            </tr>
                            <tr> 
                                <!-- <th v-for="(item, index) in headers" :key="index" >
                                    {{item.text}}
                                </th>  -->
                                
                                <!-- <th :colspan="headers.length" class="text-center">
                                Tech Power Camlocks
                                </th> -->
                            </tr>
                            </thead>
                        </template>
                    </v-data-table>
                    </v-card>
                </v-col>    
            </v-row>      
        </v-container>
  </v-card-text>
  <v-card-text v-else><v-card-subtitle class="subtitle-1">FTP Error</v-card-subtitle>
                        <v-card-subtitle>Use the above drop downs with the Quick View button for now.</v-card-subtitle> </v-card-text>
  <span>{{test2}}</span>
    </v-card>
</template>

<script>
import address from '../server'

export default {
    name:'PowerLogs',
    props:[],
    data: () => ({
        showFTP:true,
        headers:[
            {
            text:'A/B',
            align: 'center',
            value: 'tech-A-V'
            },
            {
            text:'B/C',
            align: 'center',
            value: 'tech-B-V'
            },
            {
            text:'C/A',
            align: 'center',
            value: 'tech-C-V'
            },
            {
            text:'A',
            align: 'center',
            value: 'tech-A-A'
            },
            {
            text:'B',
            align: 'center',
            value: 'tech-B-A'
            },{
            text:'C',
            align: 'center',
            value: 'tech-C-A'
            },{
            text:'A/B',
            align: 'center',
            value: 'hvac-A-V'
            },
            {
            text:'B/C',
            align: 'center',
            value: 'hvac-B-V'
            },
            {
            text:'C/A',
            align: 'center',
            value: 'hvac-C-V'
            },
            {
            text:'A',
            align: 'center',
            value: 'hvac-A-A'
            },
            {
            text:'B',
            align: 'center',
            value: 'hvac-B-A'
            },{
            text:'C',
            align: 'center',
            value: 'hvac-C-A'
            },{
            text:'Min',
            align: 'center',
            value: 'time-min'
            },{
            text:'Sec',
            align: 'center',
            value: 'time-sec'
            },
        ],

        month:[],
        monthSelect:'',
        day:[],
        daySelect:'',
        hour:[],
        hourSelect:'',
        logFile:[],
        logFileSelect:'',
        test:[],
        test2:'',
        load:false,

    }),
    created(){
        /* let d = new Date()
        let n = d.getMonth() */
        
        

        //let vm = this
        /* this.$http.get('/api/ftp/list/power_logs/RCX/POWER_LOGS',function(err,ret){
                //console.log(err)
                ret.data.results.forEach(x => {
                    vm.month.push(x)
                    
                });
            }) */
        this.$http.get('/api/ftp/list/power_logs/RCX/POWER_LOGS').then(res => {
            res.results.forEach(x => {
                this.month.push(x)
                    
            });
        })
    },
    methods:{
        hide(x){
            switch (x) {
                case 'month':
                    this.day = []
                    this.hour = []
                    this.logFile = []
                    this.daySelect = ''
                    this.hourSelect = ''
                    this.logFileSelect = ''
                    break;
                case 'day':
                    //this.daySelect = ''
                    this.hour = []
                    this.logFile = []
                    this.hourSelect = ''
                    this.logFileSelect = ''
                    break;
                case 'hour':
                    //this.daySelect = ''
                    //this.hourSelect = ''
                    this.logFile = []
                    this.logFileSelect = ''
                    break;


                default:
                    break;
            }
        },
        dropDown(type){
            let dir = ''
            switch (type) {
                case 'month':
                    dir = 'POWER_LOGS'
                    break;
                case 'day':
                    dir = `POWER_LOGS/${this.monthSelect}`
                    break;
                case 'hour':
                    dir = `POWER_LOGS/${this.monthSelect}/${this.daySelect}`
                    break;
                case 'logFile':
                    dir = `POWER_LOGS/${this.monthSelect}/${this.daySelect}/${this.hourSelect}`
                    break;
                default:
                    return
                    
            }


            dir = dir.split("/").join("-")

            //let vm = this
            /* this.$http.get(`/api/ftp/list/power_logs/RCX/${dir}`,function(err,ret){
                console.log(err)
                ret.data.results.forEach(x => {
                    vm[type].push(x)
                    
                });
            }) */
            this.$http.get(`/api/ftp/list/power_logs/RCX/${dir}`).then(res => {
                res.results.forEach(x => {
                    this[type].push(x)
                });
            })
        },
        getLogs(){ // <<<<<<<<<<<<<<<<<------------------------- i think this can be removed
            //let vm = this
            this.load = true
            /* this.$http.get(':1337',function(err,ret){
                vm.load = false
                console.log( err || ret.data)
                vm.test = ret.data.results
            }) */
            this.$http.get(':1337').then(res => {
                this.load = false
                this.test = res.results
            })
           

        },
        quickLogs(){
            console.log(address)
            let dir = this.monthSelect+"-"+this.daySelect+"-"+this.hourSelect
            window.open(`http://${address}/api/ftp/get/power_logs/RCX/POWER_LOGS-${dir}/${this.logFileSelect}`,'example','width=400,height=500');
        }
    },

}
</script>

<style scoped>
    .fs-13{
        font-size: 13px;
    }
</style>